.toggle-btn{
    padding: 10px 20px;
    margin-bottom: 40px;
    background-color: blue;
    color: #fff;
    border: none;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
}
.toggle-btn:focus {
    outline: none;
}
.app {
    background: blue url('./got.jpeg') center center no-repeat;
    background-size: cover;
    height: 1000px;	
}
.main {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 450px;
    background: url('./main.jpg') center center/cover no-repeat;
}
.main__title {
    display: flex;
    justify-content: center;
    color: #fff;
}